import React, {Component} from "react";
import {connect} from "react-redux"
import PropTypes from "prop-types";
import {OraButton, OraLoader, OraMessage} from "eshop/components/OraCommonComponents";
import LabeledInput from "../../core/components/hoc/LabeledInput";
import InputWithFloatingLabel from "../../magnum2/components/InputWithFloatingLabel";
import {isFieldDisabled} from "../utils/utils";
import {validateZipCode} from "../actions/data";

class MulticartCommonAddressForm extends Component {

    constructor(props) {
        super(props);
        this.handlePostalCode = this.handlePostalCode.bind(this);
        this.getPropsForInput = this.getPropsForInput.bind(this);
        this.validateAppartmentNoAndStreetNumber = this.validateAppartmentNoAndStreetNumber.bind(this);
        this.state = {
            hasCitySuggestions: this.props.citySuggestions && this.props.citySuggestions.length > 0,
            zipValidated: false,
            autoSelect: false
        }
    }

    compareArrays(a1, a2) {
        return !!a1 && !!a2 && a1.filter(e => a2.indexOf(e) < 0).length === 0 && a2.filter(e => a1.indexOf(e) < 0).length === 0;
    }

    componentDidUpdate(prevProps) {
        if (!this.props.town && !this.compareArrays(prevProps.citySuggestions, this.props.citySuggestions)) {
            this.setState({
                hasCitySuggestions: this.props.citySuggestions && this.props.citySuggestions.length > 0
            })
        }
        if (!!this.props['streetNumber'] &&
            this.props.validation['streetName'] &&
            this.props.validation['town'] &&
            !!this.props.townId &&
            !this.state.zipValidated) {
            this.props.validateZipCode({
                addressType: this.props.id,
                zip: this.props['postalCode'],
                townId: this.props.townId,
                streetId: this.props.streetId,
                streetNumber: this.props['streetNumber'],
            });
            this.setState({
                zipValidated: true
            });

            this.validateAppartmentNoAndStreetNumber();
        }

        if(this.isAddressWithOnlyOneStreet(prevProps)) {
            this.setState({autoSelect: true});
            this.props.onChange({id: '', name: "streetName", value: this.props.streetSuggestions[0], cbsId: ''});
        } else if(this.isAddressWithMultipleStreets(prevProps) || prevProps.postalCode != this.props.postalCode || prevProps.town != this.props.town) {
            this.setState({autoSelect: false});
        }
    }

    isAddressWithOnlyOneStreet(prevProps) {
        return prevProps.streetSuggestions && prevProps.streetSuggestions.length == 0 && this.props.streetSuggestions && this.props.streetSuggestions.length == 1;
    }

    isAddressWithMultipleStreets(prevProps) {
        return (prevProps.streetSuggestions != this.props.streetSuggestions && this.props.streetSuggestions && this.props.streetSuggestions.length > 1);
    }
    
    validateAppartmentNoAndStreetNumber(){
        this.props.onBlur({id: null, name:'appartmentNo', value: this.props['appartmentNo'] || ""});
        this.props.onBlur({id: null, name:'streetNumber', value: this.props['streetNumber'] || ""});
    }

    getPropsForInput(name) {
        let result = {
            name,
            value: this.props[name] || "",
            errors: this.props.errors && this.props.errors[name],
            valid: this.props.validation[name],
            label: this.props.descriptions[name],
            labelType: "floating",
            disabled: isFieldDisabled(this.props, name) ||
                (!this.props.addressMapping && !this.props.existing && this.props.wwtaddress) ||
                (this.props.existing && !this.props.hasFixAddress && this.props.wwtaddress),
            onChange: this.props.onChange,
            onBlur: this.onBlur.bind(this),
            className: this.props.customInputClass,
        };

        if (this.props.wwtaddress && name === "appartmentNo" && !isFieldDisabled(this.props, name)) {
            result.disabled = false;
        }

        return result;
    }

    mapToPostalCode(value) {
        if(value && typeof value === "string" && value.length === 5) {
            return value.substring(0,2) + "-" + value.substring(2,5);
        } else {
            return value;
        }
    }

    onBlur({id, name, value}) {
        if(!!this.props.onBlur) {
            this.props.onBlur({id, name, value})
        }
        this.setState({
            zipValidated: false
        })
    }

    getPropsForPostalInput(name) {
        return {
            ...this.getPropsForInput(name),
            value: this.mapToPostalCode(this.props[name]),
            onChange: this.handleChangePostalCode.bind(this),
            onBlur: this.handleBlurPostalCode.bind(this),
            mask: "99-999",
            placeholder: "__-___",
            customInputClass: this.props.customInputClass,
        };
    }

    handlePostalCode({id, name, value}, action) {
        if(value) {
            value = value.split("-").join("");
        } else {
            value = "";
        }
        action({id, name, value});
        this.setState({
            hasCitySuggestions: this.props.citySuggestions && this.props.citySuggestions.length > 0
        })
    }

    handleChangePostalCode({id, name, value}) {
        this.handlePostalCode({id, name, value}, this.props.onChange);
    }

    handleBlurPostalCode({id, name, value}) {
        if(!!this.props.onBlur) {
            this.handlePostalCode({id, name, value}, this.props.onBlur);
        }
    }

    handleAutoCompleteBlur(event) {
        const {id, value} = event;
        const name = id === 'city_id' ? 'town' : id === 'location_street' ? 'streetName' : '';
        if (this.props.onBlur) {
            this.props.onBlur({id, name, value, cbsId: id === 'city_id' ? this.props.townId : id === 'location_street' ? this.props.streetId : ''});
        }
        this.setState({
            zipValidated: false
        })
    }

    handleAutoCompleteChange(event) {
        const {id, value} = event;
        const name = id === 'city_id' ? 'town' : id === 'location_street' ? 'streetName' : '';
        if (this.props.onChange) {
            this.props.onChange({id, name, value, cbsId: ''});
        }
    }

    onCitySelect(data) {
        let {id, label = "", value} = data.item;
        if(this.props.onChange) {
            this.props.onChange({id, name: "town", value: label.toUpperCase(), cbsId: value});
        }

        OPL.UI.fire(OPL.UI.EVENTS.modules.autocomplete.querydata, {
            "city_id": data.item.value
        }, 'location_street');
    }

    onStreetSelect(data) {
        let {id, label = "", value} = data.item;
        if(this.props.onChange) {
            this.props.onChange({id, name: "streetName", value: label.toUpperCase(), cbsId: value});
        }
    }

    render() {
        const cityAutocompleteUrl = "/hapi/cbs/cityauto";
        const streetAutocompleteUrl = "/hapi/cbs/streetauto";
        return (
            <div className="opl-form">
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-6">
                        <LabeledInput {...this.getPropsForPostalInput("postalCode")} className={this.props.className}/>
                    </div>

                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-6">
                        <div className="u-spacing-l u-medium-hide u-large-hide"/>
                        {
                            this.state.hasCitySuggestions
                                ? <LabeledInput {...this.getPropsForInput("town")} autoComplete={true}
                                                suggestions={this.props.citySuggestions}
                                                className={this.props.className}/>
                                : <InputWithFloatingLabel
                                    {...this.getPropsForInput("town")}
                                    id={"city_id"}
                                    required={true}
                                    placeholder={"Miejscowość"}
                                    autocomplete={true}
                                    autocompleteUrl={cityAutocompleteUrl}
                                    onChange={this.handleAutoCompleteChange.bind(this)}
                                    onSelect={this.onCitySelect.bind(this)}
                                    onBlur={this.handleAutoCompleteBlur.bind(this)}
                                    overrideClass={`o-floating-label`}
                                    showValidation={true}
                                    showErrors={true}
                                    customInputClass={this.props.customInputClass}
                                />
                        }
                    </div>

                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12">
                        <div className="u-spacing-l "/>
                        {
                            this.state.hasCitySuggestions
                                ? <LabeledInput {...this.getPropsForInput("streetName")} autoComplete={true}
                                                suggestions={this.props.streetSuggestions}
                                                className={this.props.className}
                                                autoSelect={this.state.autoSelect}/>
                                : <InputWithFloatingLabel
                                    {...this.getPropsForInput("streetName")}
                                    id={"location_street"}
                                    required={true}
                                    placeholder={"Ulica"}
                                    autocomplete={true}
                                    autocompleteUrl={streetAutocompleteUrl}
                                    onChange={this.handleAutoCompleteChange.bind(this)}
                                    onSelect={this.onStreetSelect.bind(this)}
                                    onBlur={this.handleAutoCompleteBlur.bind(this)}
                                    overrideClass={`o-floating-label`}
                                    showValidation={true}
                                    showErrors={true}
                                    data={{
                                        "city_id": this.props.townId
                                    }}
                                    customInputClass={this.props.customInputClass}
                                    autoSelect={this.state.autoSelect}
                                />
                        }
                        <div className="u-spacing-l "/>
                    </div>

                    <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                        <LabeledInput {...this.getPropsForInput("streetNumber")} valid={(!this.props.errors && this.props["streetNumber"]!=='') || (this.props.errors && this.props.errors["streetNumber"] && (this.props.errors["streetNumber"].length === 0))} className={this.props.className}/>
                    </div>

                    <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                        <LabeledInput {...this.getPropsForInput("appartmentNo")} valid={(!this.props.errors && this.props["appartmentNo"]!=='') || (this.props.errors && this.props.errors["appartmentNo"] && (this.props.errors["appartmentNo"].length === 0))} className={this.props.className}/>
                    </div>
                </div>
            </div>

        )
    }
}

MulticartCommonAddressForm.propTypes = {
    postalCode: PropTypes.string.isRequired,
    town: PropTypes.string.isRequired,
    streetName: PropTypes.string.isRequired,
    streetNumber: PropTypes.string.isRequired,
    appartmentNo: PropTypes.string.isRequired,
    citySuggestions: PropTypes.array,
    streetSuggestions: PropTypes.array,
    readOnly: PropTypes.bool,
    onChange: PropTypes.func.isRequired,
    customInputClass: PropTypes.string
};

const mapDispatchToProps = dispatch => ({
    validateZipCode: (address) => dispatch(validateZipCode(address))
});

export default connect((state) => ({}), mapDispatchToProps)(MulticartCommonAddressForm);